import { request,getToken,Token } from '../request.js'
export default {
	data() {
		return {
			statusHeight: '',
			baseURL:this.$api,
			isLogin:true,
			user_name:'未登陆',
			avatar:'/static/images/user-default.jpeg',
			ordernum:{
				0:0,
				1:0,
				2:0
			},
			area:'',
			// 储存推广的uid
			uid: ''
		};
	},
	onShow() {
		this.getLogin();
		uni.getSystemInfo({
			success: res => {
				this.statusHeight = res.statusBarHeight + 'px'
			}
		})
	},
	onLoad() {
		// Token()
	},
	methods:{
		// 跳转到登录页
		login() {
			uni.navigateTo({
				url: '../components/login/login'
			})
		},
		// 跳转到注册页
		register() {
			uni.navigateTo({
				url:'../components/register/register'
			})
		},
		// 跳转到order我的订单页
		order(status) {
			uni.navigateTo({
				url: '/pages/components/order/order?status='+status
			})
		},
		// 跳转到收藏页
		collect() {
			uni.navigateTo({
				url: '/pages/components/collect/collect'
			})
		},
		// 跳到收货地址
		address() {
			uni.navigateTo({
				url:'/pages/components/address/address'
			})
		},
		// 跳转到我的钱包页面
		money() {
			uni.navigateTo({
				url: '/pages/components/money/money'
			})
		},
		getLogin() {
			let that = this;
			request({
					url:"index.php?s=/wap/member/checkLogin",
					method:"POST",
				}).then(res=>{
					// 未登录
					if(res.data.code == 201) {
						this.isLogin = false
					}else{
						this.isLogin = true;
						that.getUserInfo();
					}
				});
		},
		getUserInfo(){
			let that = this;
			
			request({
					url:"index.php?s=/wap/member/Api_member_index",
					method:"POST",
				}).then(function(res){
					// 储存推广的uid
					that.uid = res.data.uid;
					that.user_name = res.data.user_name;
					that.avatar = that.baseURL + res.data.member_img;
					that.ordernum = {
						0:res.data.unpaidOrder,
						1:res.data.shipmentPendingOrder,
						2:res.data.goodsNotReceivedOrder,
					};
					let address = res.data.address;
					that.area = address.province + " " + address.city + " " + address.district;
				});
		},
		// 跳到我的订单
		// bill() {
		// 	uni.navigateTo({
		// 		url:'/pages/components/bill/bill'
		// 	})
		// },
		// 跳转到商家入驻页面
		goBusiness() {
			uni.navigateTo({
				url: '/pages/components/business/business'
			})
		},
		// 跳转到我的推广页面
		tuiguang() {
			uni.navigateTo({
				url: '/pages/components/generalize/generalize?uid='+this.uid
			})
		},
		
		// 跳转到个人中心页
		userInfo() {
			uni.navigateTo({
				url: '/pages/components/userInfo/userInfo'
			})
		}
	},
	
}
